// Normally mainpage styles

body
	background-image: url(../img/wooden.jpg)
	position: relative

$border-color: #EFEFEF

.stripped-bg
	position: absolute
	z-index: -1
	height: 397px
	top: 230px
	width: 100%
	background-image: url(../img/strips.png)

.headind-stripped
	width: 100%
	line-height: 25px
	position: relative
	overflow: hidden
	vertical-align: middle
	white-space: nowrap
	&__p
		font-family: "Georgia Italic", sans-serif
		font-size: 0.9rem
		font-style: Italic
		margin-right: 10px
		vertical-align: middle
		display: inline-block
	&__line
		display: inline-block
		height: 11px
		width: 100%
		background-image: url(../img/strips.png)
		overflow: hidden
		vertical-align: middle



.container
	max-width: 980px
	margin: 0 auto


*
	transition: all 0.3s ease


.header-wrap
	height: 245px
	width: 100%
	background-color: rgba(0,0,0,.8)
	position: relative


.header-topline
	height: 36px
	line-height: 36px
	background-color: rgba(0,0,0,.3)
	font-family: "Georgia Italic", sans-serif
	font-size: 0.75rem
	font-style: Italic
	z-index: 1
	+clr

.date
	display: inline-block
	color: #fff
	margin-right: 5px

.topline
	position: relative
	ul
		display: inline-block
		margin: 0
		padding: 0
		li
			display: inline-block
			&:hover
				> a
					color: #fff
					opacity: 1
			.submenu
				display: none
				width: 160px
				background-color: #221E17
				position: absolute
				z-index: 10
				padding: 0 15px 20px 15px
				border-bottom-left-radius: 8px
				border-bottom-right-radius: 8px
				box-shadow: 1px 2px 2px #000
				li
					display: block
					height: 32px
					line-height: 20px
					border-bottom: 1px solid rgba(255,255,255,.3)
					&:last-child
						border-bottom-color: transparent
					a
						border-left: none
						line-height: 32px
						display: block
			a
				color: #fff
				text-decoration: none
				padding-right: 10px
				padding-left: 10px
				border-left: 1px solid rgba(255,255,255,.5)
				opacity: .5
				&#no-border
					border-left-color: transparent
					&:hover
						border-left-color: transparent
				&:hover
					color: #fff
					opacity: 1
					border-left-color: rgba(255,255,255,.25)
				.fa
					font-size: 12px
					margin-left: 2px

.subscribe
	position: absolute
	right: 0
	top: 0
	color: rgba(255,255,255,.5)
	a
		color: rgba(255,255,255,.7)
		text-decoration: none
		padding-left: 3px
		padding-right: 3px
		&:hover
			color: rgba(255,255,255,.9)
//menu end

.header-bot
	position: relative

.logo
	position: absolute
	top: 50px
	left: 25px
	a
		font-family: "Ronnia Bold Italic", sans-serif
		font-style: Italic
		font-weight: Bold
		font-size: 34px
		color: #fff
		text-decoration: none
		display: inline-block
		vertical-align: middle
		margin-right: 10px
	span
		font-family: "Georgia Italic", sans-serif
		font-size: 0.75rem
		font-style: Italic
		display: inline-block
		line-height: 12px
		color: #fff
		opacity: .5
		padding-left: 5px
		border-left: 1px solid rgba(255,255,255,.5)

.banner
	position: absolute
	width: 468px
	height: 60px
	right: 0
	top: 36px
	overflow: hidden
	border: 1px solid rgba(255,255,255,.5)
	&:hover
		border-color: #fff
	a
		display: block
		height: 100%
		img
			height: 100%
			width: 100%

.main-nav
	height: 65px
	width: 100%
	background-color: rgba(0,0,0,0.2)
	border-top-left-radius: 8px
	border-top-right-radius: 8px
	position: absolute
	bottom: -195px
	border-bottom: 8px solid #000
	color: #fff
	padding-left: 15px



.main-nav-ul
	margin: 0
	padding: 0
	li
		display: inline-block
		position: relative
		margin-right: 0px
		padding: 12px 10px 0 10px
		&:hover
			background-color: #222222
		a
			color: #fff
			font-family: "Helvetica Neue", sans-serif
			font-weight: Bold
			font-size: 15px
			text-decoration: none
			text-transform: capitalize
		span
			font-family: "Georgia Italic", sans-serif
			font-size: 0.70rem
			font-style: Italic
			color: #fff
			opacity: .5
			display: inline-block
		.submenu-2
			display: none
			position: absolute
			left: 0px
			width: 100%
			background-color: #222222
			z-index: 100
			padding: 10px 15px 10px 10px
			border-bottom-left-radius: 8px
			border-bottom-right-radius: 8px
			li
				line-height: 30px
				display: block
				border-bottom: 1px solid rgba(255,255,255,.3)
				&:last-child
					border-bottom-color: transparent
				a
					font-family: "Georgia Italic", sans-serif
					font-size: 0.9rem
					font-style: Italic
					color: #fff
					opacity: .5
					margin-bottom: 5px
					display: block
					&:hover
						opacity: 1



// main

.main
	margin-top: -15px
	background-color: #fff
	box-shadow: 0 0 10px rgba(0,0,0,0.5)
	position: relative
	overflow: hidden
	padding: 15px 25px
	z-index: 10

.stripped-line
	width: 100%
	height: 16px
	background-image: url(../img/strips.png)
	margin-bottom: 15px

.stripped-line-2
	width: 100%
	height: 36px
	background-image: url(../img/strips.png)
	margin-bottom: 25px
	font-family: "Georgia Italic", sans-serif
	font-size: 0.9rem
	font-style: Italic
	

.to-top
	right: 25px
	text-decoration: none
	color: #787878
	line-height: 36px
	float: right
	margin-right: 25px
	.fa
		font-size: 11px
		&:hover
			color: #ddd

// slider

.item
	+br(8)
	background-color: #ccc
	height: 100%
	position: relative
	overflow: hidden
	z-index: 1
	&-img
		width: 100%
		height: 100%
		z-index: 2

.sliders-wrap
	position: relative
	height: 300px
	width: 100%
	margin-bottom: 15px
	vertical-align: middle

.text-wrap-slide
	position: absolute
	top: 30px
	left: 30px
	z-index: 10
	h3
		color: #fff
		font-family: "Helvetica Neue", sans-serif
		font-weight: Bold
		font-size: 18px
		padding: 15px
		background-color: rgba(0,0,0,0.5)
		border-top-left-radius: 8px
		border-top-right-radius: 8px
		border-bottom-right-radius: 8px
		margin: 0
	p
		display: inline-block
		background-color: rgba(0,0,0,0.5)
		border-bottom-left-radius: 8px
		border-bottom-right-radius: 8px
		font-family: "Georgia Italic", sans-serif
		font-size: 0.9rem
		font-style: Italic
		color: rgba(255,255,255,0.5)
		padding: 11px 15px 18px 15px
		margin: 0px
		line-height: 0
		a
			color: rgba(255,255,255,0.7)
			text-decoration: none
			&:hover
				color: rgba(255,255,255,0.9)

.slider-control
	position: absolute
	width: 740px
	height: 82px
	top: 100%
	left: 50%
	transform: translate(-50%, -50%)
	background-color: #fff
	+br(8)
	overflow: hidden
	padding: 10px 40px
	&__item
		width: 100px
		height: 62px
		background-color: #fff
		box-shadow: 0 0 7px rgba(0,0,0,0.5)
		border: 1px solid #B8B8B8
		display: block
		margin-left: 5px
		margin-right: 5px
		position: relative
		&:hover, 
			border-color: #333
			background-color: #666
			cursor: pointer
		img
			width: calc((100%) - (3px))
			height: calc((100%) - (3px))
			+vcenter

.slick-current
	border-color: #333
	background-color: #666

.arrow-left
	width: 20px
	height: 62px
	color: #333
	background-color: #EFEFEF

.arrow-right
	width: 20px
	height: 62px
	color: #EFEFEF
	background-color: #333

.slick-slider
	vertical-align: middle

.prev-custom,
.next-custom
	display: inline-block
	height: 62px
	width: 20px
	position: absolute
	vertical-align: middle
	z-index: 300
	line-height: 60px
	background-color: #F6F6F6
	border: 1px solid #E2E2E2
	text-align: center
	top: 50%
	transform: translateY(-50%)
	&:hover
		color: #fff
		background-color: #383838
		border-color: #202020
		cursor: pointer

.prev-custom
	border-top-left-radius: 8px
	border-bottom-left-radius: 8px
	left: 10px


.next-custom
	border-top-right-radius: 8px
	border-bottom-right-radius: 8px
	right: 10px

// main-content-wrap

.main-content-wrap
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: row
	justify-content: space-between
	width: 100%

.right-bar
	width: 65%
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: column

.news-item
	width: 50%
	margin-right: 25px
	margin-bottom: 45px
	p
		font-family: "Arial", sans-serif
		font-size: 12px
		margin-bottom: 20px
		margin-top: 12px
		line-height: 20px
		color: #555

.news-row
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: row

.news-img-wrap
	width: 100%
	height: 165px
	border-bottom: 1px solid #A3A3A3
	background-color: #fff
	box-shadow: 0 0 6px rgba(0,0,0,0.5)
	position: relative
	margin-top: 15px
	margin-bottom: 20px
	&:hover
		opacity: .7
	img
		width: calc((100%) - (3px))
		height: calc((100%) - (3px))
		+vcenter

.news-title
	color: #444
	font-family: "Helvetica Neue", sans-serif
	font-size: 13px
	text-decoration: none
	margin-bottom: 15px
	display: block
	&:hover
		color: #e47600

.news-date, .news-comments
	font-family: "Georgia Italic", sans-serif
	font-size: 0.9rem
	font-style: Italic
	color: rgba(0,0,0,0.5)
	text-decoration: none
	margin-right: 4px
	display: inline-block
	&:hover
		color: rgba(0,0,0,0.7)

.separator
	height: 1px
	width: 100%
	background-color: #EFEFEF

.sub-news
	padding-bottom: 20px
	padding-top: 20px
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	justify-content: space-between
	align-items: center
	margin-right: 12px

.sub-news-img
	display: block
	height: 50px
	width: 50px
	border-bottom: 1px solid #A3A3A3
	background-color: #fff
	box-shadow: 0 0 6px rgba(0,0,0,0.5)
	position: relative
	&:hover
		opacity: .7
	img
		width: calc((100%) - (2px))
		height: calc((100%) - (2px))
		+vcenter

.sub-text-wrap
	width: 75%
	p.sub-p
		margin: 0
		font-family: "Helvetica Neue", sans-serif
		font-size: 12px
		color: #444

.latest
	margin-right: 25px
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: column
	margin-bottom: 60px

.latest-item
	margin-top: 20px
	margin-bottom: 20px
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: row
	align-items: center

.latest-text-wrap
	width: 75%
	p.sub-p
		margin: 0
		font-family: "Helvetica Neue", sans-serif
		font-size: 12px
		color: #444
		margin-top: 12px

.latest-img
	display: block
	height: 100px
	width: 180px
	border-bottom: 1px solid #A3A3A3
	background-color: #fff
	box-shadow: 0 0 6px rgba(0,0,0,0.5)
	position: relative
	margin-right: 15px
	&:hover
		opacity: .7
	img
		width: calc((100%) - (3px))
		height: calc((100%) - (3px))
		+vcenter

.add-bar
	width: 35%
	border-left: 1px solid $border-color
	padding-left: 25px
	margin-bottom: 30px

.search
	margin-bottom: 30px
	input
		width: 100%
		height: 43px
		border: 1px solid $border-color
		padding-left: 10px
		box-shadow: inset 0 0 4px rgba(0,0,0,0.3)

.banner2
	margin-top: 20px
	margin-bottom: 30px
	height: 250px
	border: 1px solid $border-color
	a 
		height: 100%
		display: block
		img
			width: 100%
			height: 100%
		&:hover
			opacity: .7

.sponsor-img-wrap
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: row
	justify-content: space-between

.sponsor-img
	width: 145px
	height: 145px
	position: relative
	margin-top: 15px
	margin-bottom: 30px
	background-color: #D5D5D5
	border: 10px solid #ccc
	&:hover
		border-color: red
	img
		width: 100%
		height: 100%
		+vcenter

.video
	margin-top: 20px
	margin-bottom: 30px

.twitter
	margin-top: 20px
	margin-bottom: 30px

.footer-wrap
	width: 100%
	background-color: rgba(0,0,0,.8)
	position: relative
	margin-top: -75px

.footer-bottom
	height: 50px
	line-height: 50px
	.copyright
		padding-left: 20px

.footer
	display: -webkit-flex
	display: -moz-flex
	display: -ms-flex
	display: -o-flex
	display: flex
	flex-direction: row
	justify-content: space-between
	padding: 110px 25px 50px 25px

.footer-item
	width: 25%
	padding-right: 20px
	color: #999
	.headind-stripped
		.headind-stripped__p
		color: #bbb

.footer-menu
	margin: 0
	padding: 0
	list-style: none
	li
		line-height: 40px
		border-bottom: 1px solid #666
	a
		text-decoration: none
		font-family: "Helvetica Neue", sans-serif
		font-size: 11px
		color: #999
		line-height: 8px
		&:hover
			color: #fff

.about
	font-family: "Helvetica Neue", sans-serif
	font-size: 12px
	margin-top: 10px

.comment-bottom
	font-family: "Helvetica Neue", sans-serif
	font-size: 12px
	margin-top: 10px
	padding-bottom: 15px
	border-bottom: 1px solid #666
	a 
		color: #ddd
		text-decoration: none
		&:hover
			color: #fff

.last-footer
	text-align: right
	a 
		color: #fff
		text-decoration: none
		font-family: "Ronnia Bold Italic", sans-serif
		font-style: Italic
		font-weight: Bold
		font-size: 34px
	p 
		font-family: "Helvetica Neue", sans-serif
		font-size: 12px
		margin-top: 10px